import React from 'react';


//page3
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import 'photoswipe/dist/photoswipe.css'
import { Gallery, Item } from 'react-photoswipe-gallery'
import mTHUMBNAIL from '../assets/thumbnail_small.jpg'
import mFPGA1 from '../assets/FPGA_group_small.jpg';
import mFPGA2 from '../assets/FPGA_personal1_small.jpg';
import mFPGA3 from '../assets/FPGA_personal2_small.jpg';
import mFPGA4 from '../assets/FPGA_game_small.jpg';
import THUMBNAIL from '../assets/thumbnail.jpg'
import FPGA1 from '../assets/FPGA_group.jpg';
import FPGA2 from '../assets/FPGA_personal1.jpeg';
import FPGA3 from '../assets/FPGA_personal2.jpeg';
import FPGA4 from '../assets/FPGA_game.jpg';



//scrolling
//containers for navigation
export default function Hardware() {

    const EE2026_Mini = [mTHUMBNAIL, mFPGA1, mFPGA2, mFPGA3, mFPGA4] 
    const EE2026 = [THUMBNAIL, FPGA1, FPGA2, FPGA3, FPGA4]

    return (
        <div>
            <div style={{display:'flex', flex:1, justifyContent:'center', alignItems:'end'}}>
                <p style={{fontSize:20, fontWeight:'bold', color:'white'}}>My Heart(hard)ware Projects</p>
            </div>

            <div style={{display:'flex', flex:6}}>
                <VerticalTimeline >

                    <VerticalTimelineElement
                        contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                        contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                        date="2021 Nov"
                        iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}>
                        
                        <p style={{fontWeight:'bold'}}>EG1311 - Design and Make</p>
                        <p style={{fontSize:12, marginBottom:10}}>
                        Upon detection of an obstacle in the immediate vicinty (via the ultrasonic sensor), 
                        the robot activates the servo motor to hurl the ball over the obstacle.
                        </p>
                        
                        <div style={{display:'flex',marginLeft:15} }>
                        <iframe src={`https://www.youtube.com/embed/-e9u6aWoF1Q`} allowFullScreen width="60%" height="100%"/>
                        </div>
                    </VerticalTimelineElement>

                    <VerticalTimelineElement
                        contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                        contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                        date="2022 Nov"
                        iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}>
                        
                        <p style={{fontWeight:'bold'}}>CG1111A - Engineering Principles and Practices I</p>
                        <p style={{fontSize:12, marginBottom:10}}>
                        The mbot conducts straffing based on the ultrasonic and infrared sensors mounted on both 
                        sides of the robot. Color detection is achieved via the LED and LDR sensors located at the 
                        bottom of the vehicle. 
                        </p>
                        
                        <div style={{display:'flex',marginLeft:15} }>
                        <iframe src={`https://www.youtube.com/embed/nJr4PbxbPn8`} allowFullScreen width="60%" height="100%"/>
                        </div>
                    </VerticalTimelineElement>

                    <VerticalTimelineElement
                        contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                        contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                        date="2023 Jan"
                        iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}>
                        
                        <p style={{fontWeight:'bold'}}>CG2111A - Engineering Principles and Practices II</p>
                        <p style={{fontSize:12, marginBottom:10}}>
                        In replacement of the breadboard, we created our own circuit by soldering relevant components and wirewrapping them subsequently. 
                        The logic of the LED is encoded using baremetal programming. 
                        </p>
                        
                        <div style={{display:'flex',marginLeft:15} }>
                        <iframe src={`https://www.youtube.com/embed/JsvSBepJblw`} allowFullScreen width="60%" height="100%"/>
                        </div>
                    </VerticalTimelineElement>
                    
                    <VerticalTimelineElement
                        contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                        contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                        date="2023 Apr"
                        iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}>
                        
                        <p style={{fontWeight:'bold'}}>EE2026 - Digital Design</p>
                        <p style={{fontSize:12, marginBottom:10}}>
                            By applying what we learnt about digital circuits and Finite State Machines, 
                            my group and I coded out complex functionalities and games using Verilog on the FPGA. 
                            <br />
                            Tap the photo to see other pictures! 
                        </p>
                        
                        <div style={{display:'flex',marginLeft:15} }>
                            <Gallery>
                                <Item original={EE2026[0]} thumbnail={EE2026_Mini[0]} width="720" height="488">
                                    {({ ref, open }) => (<img ref={ref} onClick={open} src={EE2026_Mini[0]} />)}
                                </Item>
                                <Item original={EE2026[1]} thumbnail={EE2026_Mini[1]} width="720" height="488">
                                    {({ ref, open }) => (<img ref={ref} onClick={open} src={EE2026_Mini[1]} />)}
                                </Item>
                                <Item original={EE2026[2]} thumbnail={EE2026_Mini[2]} width="720" height="488">
                                    {({ ref, open }) => (<img ref={ref} onClick={open} src={EE2026_Mini[2]} />)}
                                </Item>
                                <Item original={EE2026[3]} thumbnail={EE2026_Mini[3]} width="720" height="488">
                                    {({ ref, open }) => (<img ref={ref} onClick={open} src={EE2026_Mini[3]} />)}
                                </Item>
                                <Item original={EE2026[4]} thumbnail={EE2026_Mini[4]} width="488" height="720">
                                    {({ ref, open }) => (<img ref={ref} onClick={open} src={EE2026_Mini[4]} />)}
                                </Item>
                                
                            </Gallery>
                            
                        </div>
                    </VerticalTimelineElement>
                
                </VerticalTimeline>
            </div>
        </div>
    );
}

