import './App.css';
import { Parallax, ParallaxLayer} from '@react-spring/parallax';
import React from 'react';
import BG1 from './assets/bg1.jpg';

//page1
import COVER from './pages/Cover.js';

//page2
import SELFINTRODUCTION from './pages/SelfIntroduction';

//page3
import HARDWARE from './pages/Hardware';

//page5
import SOFTWARE from './pages/Software';


function App() {

    return (
      <div style={{height:"100vh", backgroundImage:`url(${BG1})`, backgroundSize:'100% 100%'}}>

        <Parallax class="scrollWindow" pages={6}>
          <ParallaxLayer>
            <COVER/>
          </ParallaxLayer>
          

          {/*page 2 - self intro*/}
          <ParallaxLayer offset={1} speed={1} style={{display:'flex', flexDirection:"row", justifyContent:"center",}}>
              <SELFINTRODUCTION />
          </ParallaxLayer>

          {/*page 3 - vertical timeline for hardware projects*/}
          <ParallaxLayer offset={2} style={{display:'flex', flex:1, flexDirection:'column'}}>
            <HARDWARE />
          </ParallaxLayer>

          {/*page 5 - cards for software projects*/}
          <ParallaxLayer offset={5} style={{ display:'flex', flex:1, flexDirection:'column', marginTop:30}}>
            <SOFTWARE/>
          </ParallaxLayer>
          
        </Parallax>
      </div>
    );
}

export default App;
