import {React, useEffect}from 'react';
import profile from '../assets/profile.jpg';
import AMDLogo from "../assets/AMD.png"

import { motion, useAnimation } from "framer-motion";
import { useInView } from "react-intersection-observer";



export default function SelfIntroduction() {
    const control = useAnimation();
    const [ref, inView] = useInView();

    const boxVariant = {
      visible: { opacity: 1, scale: 1, transition: { duration: 1.0 } },
      hidden: { opacity: 0, scale: 0.5 }
    };

    useEffect(() => {
      if (inView) {
        control.start("visible");
      } else {
        control.start("hidden");
      }
    }, [control, inView]);

    return (
          <motion.div className="box" ref={ref} variants={boxVariant} initial="hidden" animate={control} style={{display:'flex', flexDirection:"row", justifyContent:"center",}}>
            <div style={{display:'flex', justifyContent:"center", alignItems:'center', backgroundColor:'white', width:'70%', height:"60%", borderRadius:10}}>
                <div style={{display:'flex', alignItems:"center", width:"45%"}}>
                  <img style={{width:"100%", height:"70%"}} src={profile}/>
                </div>

                <div style={{height:"70%",width:"40%", display:'flex', flexDirection:"column", alignItems:'flex-end'}}>

                  <div style={{display:'flex', flexDirection:'column', justifyContent:'flex-end', alignItems:'center', width:"88%" }}>
                    <h1 style={{fontSize:20}}>About Me</h1>
                    <p style={{fontSize:13}}>
                    Hi, I am Zheng Xi, a Computer Engineering undergraduate at National University of Singapore. 
                    I am currently an intern at AMD performing various Proof Of Concepts (POC) on virtualisation and cloud 
                    technologies. My passion lies in the development of Artifical Intelligence as well as its 
                    application in our daily lives.
                    </p>
                    <img src={AMDLogo} style={{width:"15%", height:"15%"}}/>
                    <a href='https://www.linkedin.com/in/zheng-xi-eu-b019021bb/' style={{cursor:'pointer', fontSize:13, marginTop:10}}>Connect with me via LinkedIn!</a> 
                  </div>

                  
                  

                </div>

      
            </div>
          </motion.div>

    );
}

