import React, {useState} from 'react';

//page4
import TinderCard from 'react-tinder-card';
import '../styles.css'
import u2 from '../assets/u2.png';
import u3 from '../assets/u3.png';
import u4 from '../assets/u4.png';
import u5 from '../assets/u5.png';



//scrolling
//containers for navigation
export default function Software() {

   const UIUX = [u5, u2, u4, u3];

   const [index, setIndex] = useState(0);
   const swe = [
    {url:u3, title:"Telebot - Walk SomeMore", 
      des:'Want to make it for the next bus but you are unsure how fast your pace needs to be? Check out the Walk Some-More telebot - a customised pacer that is guaranted to get you in time for the bus! This telebot retrieves real time bus timings from the LTA API and calculates the pace necessary to ensure you hop on the next bus! Just simply walk according to the pace set by the video sent by the telebot!', 
      link:"https://devpost.com/software/WalkSomeMore"},
    {url:u4, title:"Tinder? Nope Hair Checker", 
      des:'NSFs (National Servicemen Full-Time) are required to maintain a trim and proper haircut. Often, haircuts and hair lengths we deem to be acceptable do not meet the requirements of defence force. NSFs can now upload a picture of their haircut and the AI will estimate if the haircut is acceptable. The dataset used to train the AI will be provided by commanders who will swipe left if a haircut is unacceptable and right otherwise. ', 
      link:'https://github.com/thebadone231/ns-journey'}, 
    {url:u2, title:"plus-one", 
      des:'Ever wanted that bubbletea so badly but you are unwilling to pay the exorbitant delivery price? What if the aunty living next door happens to be near the bubbletea store and she is willing to bring back the beverage for a small fee? Plus-one promises to be an affordable substitute for delivery services. Pay a small fee for someone to run an errand on your behalf or get rewarded when you complete errand request on behalf of others!', 
      link:'https://github.com/thebadone231/plus-one'}, 
    {url:u5, title:"N queens? N kings, N knights, N bishops....", 
      des:'This is an adaptation of the famous N queens problem where there are n pieces asides from the queen. This is a project of the CS3243 Introduction to Artificical Intelligence module. By framing this scenario as a constraint satisfaction problem, it can be resolved by running the backtracking algorithm - recursive DFS with forward check / AC3 algorithm', 
      link:''},  
  ]

    return (
      <div style={{ display:'flex', flex:1, flexDirection:'column', marginTop:30}}>
            <div style={{display:'flex', flex:1, justifyContent:'center'}}>
              <p style={{marginTop:50, fontSize:25, fontWeight:700, color:'white'}}>My Software Projects (try holding and dragging the cards!)</p>
            </div>

            <div style={{display:'flex', flex:12, justifyContent:'center',}}>
              <div style={{flex:45, width:'70%', display:'flex', justifyContent:'end'}}>
                {UIUX.map(pic => 
                  <TinderCard className='swipe' onSwipe={(dir) => {setIndex(index + 1)}}>
                      <div style={{ backgroundImage: index < swe.length ? 'url(' +swe[index].url + ')': + 'url()'}} className='card'>
                      <h1></h1>
                      </div>
                    </TinderCard>)}
              </div>

              <div style={{flex:55, display:'flex', alignItems:'center'}}>
                <div style={{width:'50%', height:'90%', display:'flex', flexDirection:'column', marginLeft:60}}>
                  <p style={{color:'white', fontWeight:'bold', fontSize:20}}>{index < swe.length ? swe[index].title : "That's all, Thank You!"}</p>
                  <p style={{color:'white'}}>{index < swe.length? swe[index].des : ''}</p>
                  <a style={{color:'white',cursor: 'pointer'}} href={index < swe.length? swe[index].link : ''}>{index < swe.length? swe[index].link : ''}</a>
                </div>
              </div>
            </div>

      </div>
    );
}
